
body,
html {
  width: 100%;
  height: 100%;
  min-height: 100%;
  overflow-x: hidden;
}
html {
  font-size: 100px;
}
@media screen and (min-width: 320px) {
html {
    font-size: 75px;
}
}
@media screen and (min-width: 375px) {
html {
    font-size: 100px;
}
}
@media screen and (min-width: 768px) {
html {
    font-size: 140px;
}
}
#app {
  width: 100%;
  height: 100%;
/*footer*/
}
#app .wrapper {
  width: 100%;
  height: 100%;
  font-size: 0.14rem;
  max-width: 750px;
  margin: 0 auto;
  text-align: center;
  background: url("../static/images/bg.jpg");
  background-size: 100% 100%;
}
#app .wrapper header {
  position: relative;
  padding: 0.2rem 2%;
  text-align: center;
  font-size: 0.16rem;
  color: #fff;
}
#app .wrapper header .arrowLeft {
  position: absolute;
  font-weight: 700;
  font-size: 0.2rem;
  left: 0.08rem;
}
#app .wrapper header .titleWord {
  font-weight: 200;
  font-size: 0.16rem;
}
#app .wrapper .avatarBox {
  width: 80%;
  min-height: 50px;
  margin: 0.2rem auto;
  overflow: hidden;
  text-overflow: ellipsis;
}
#app .wrapper .avatarBox ul {
  width: 500%;
}
#app .wrapper .avatarBox ul .avatarItems {
  width: 0.5rem;
  height: 0.5rem;
  float: left;
  margin-left: 10px;
  margin-top: 5px;
  background: url("../static/images/defaultAvatar.png");
  background-size: 100% 100%;
  border-radius: 50%;
}
#app .wrapper .avatarBox ul .avatarItems img {
  width: 0.5rem;
  height: 0.5rem;
  border-radius: 50%;
}
#app .wrapper .rewardPeople {
  padding: 0.2rem;
  font-size: 0.16rem;
  font-weight: 200;
  color: #fff;
}
#app .wrapper .mainAvatar img {
  border-radius: 50%;
  display: block;
  margin: 0 auto;
}
#app .wrapper .pricebox {
  padding-top: 0.2rem;
}
#app .wrapper .pricebox > ul > li {
  box-sizing: border-box;
  list-style: none;
  float: left;
  width: 33%;
  height: 50%;
  margin-top: 10px;
  text-align: center;
  padding: 0 3%;
}
#app .wrapper .pricebox > ul > li span {
  border: 1px solid #f56766;
  border-radius: 10px;
  display: block;
  text-decoration: none;
  color: #f56766;
  padding: 8px;
}
#app .wrapper .pricebox > ul > li.active span {
  color: #000;
  border-color: #000;
}
#app .wrapper .buttonBox {
  margin-top: 20px;
}
#app .wrapper .buttonBox button {
  width: 1.2rem;
  height: 0.36rem;
  background-color: #f56766;
  margin: 0 auto;
  color: #fff;
  font-size: 16px;
  border-radius: 5px;
  border: none;
}
#app footer {
  text-align: center;
  padding-top: 0.3rem;
  color: #f56766;
  font-size: 0.16rem;
  line-height: 1.8;
}
#app footer .logo {
  padding: 0.5rem 37% 0.1rem 37%;
}
#app footer .logo img {
  width: 100%;
  margin: 0 auto;
}
#app .bulletinWrapper {
  position: fixed;
  width: 100%;
  height: 100%;
/*background-color #7e8c8d*/
  top: 0;
  left: 0;
  z-index: 9;
}
#app .bulletinWrapper .popimg {
  width: 100%;
  height: 50%;
  background: url("../static/images/pop.png");
  background-size: 100% 100%;
  position: absolute;
  top: 50%;
  font-size: 0.16rem;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
#app .bulletinWrapper .popimg .popbox {
  padding: 30% 20%;
  color: #a13103;
}
#app .bulletinWrapper .popimg .popbox .text {
  margin-top: 20px;
  line-height: 1.2;
}


